// import actionTypes from '../consts/actionTypes';
import createReducer from './createReducer';

const reducers = {};

const defaultState = {

};

reducers[actionTypes.workers.WORKERS_FETCH] = state => ({
  ...state,
  fetching: true,
});

reducers[actionTypes.workers.WORKERS_FETCH_SUCCESS] = (state, { workers }) => ({
  ...state,
  fetching: false,
  workers
});


export default createReducer(reducers, defaultState);
